___CSS_LOADER_EXPORT___.push([module.id, `* {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
    display: grid;
    height: 100vh;
    width:fit-content;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(5, 1fr);
    overflow: scroll;
    background-color: #87CEEB;
}

#search{
    grid-column: 1/3;
    grid-row: 1/2;
    height: 50px;
    appearance: none;
    background: white;
    border-radius: 15px;
    border-top: solid black 1px;
    border-bottom: solid black 1px;
    border-left: solid black 1px;
    border-right: none;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    outline: none;
    padding-left: 10px;
}

#searchbutton {
    height: 50px;
    grid-column: 3/4;
    appearance: none;
    background: white;
    border-radius: 15px;
    border-top: solid black 1px;
    border-bottom: solid black 1px;
    border-right: none;
    border-left: none;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    color: skyblue;
    font-weight: bolder;
}

#c {
    height: 50px;
    grid-column: 4/5;
    appearance: none;
    background: white;
    border-radius: 15px;
    border-top: solid black 1px;
    border-bottom: solid black 1px;
    border-left: none;
    border-right: none;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    color: skyblue;
    font-weight: bolder;
}

#f {
    height: 50px;
    grid-column: 5/6;
    appearance: none;
    background: white;
    border-radius: 15px;
    border-top: solid black 1px;
    border-bottom: solid black 1px;
    border-left: none;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    color: skyblue;
    font-weight: bolder;
}

#searchbutton:hover,
#f:hover,
#c:hover {
    background-color: #87ceeb;
    color: white;
}

.currentgroup {
    margin-top: 10px; 
    padding: 20px;
    padding-top: 0px;
}

.cityname{
    grid-column: 1/-1;
    grid-row: 1/2;
    text-align: start;
    font-size:40px;
}

.condition {
    grid-row: 4/5;
    grid-column: 1/2;
    text-align: start;
}

.temp {
    grid-row: 2/3;
    grid-column: 2/3;
    text-align: start;
}

.feelslike {
    grid-row: 3/4;
    grid-column: 2/3;
    text-align: start;
}


.date {
    text-align: start;
    grid-row: 2/3;
    grid-column: 1/2;
}

.time {
    grid-column: 1/2;
    grid-row: 3/4;
    text-align: start;
}


.wind {
    grid-column: 2/3;
    grid-row: 4/5;
    text-align: start;
}

.gust {
    grid-column: 2/3;
    grid-row: 1/2;
    text-align: start;
    align-self: center;
}


.conditionicon {
    grid-row: 2/4;
    grid-column: 3/5;
    align-self: center;
    justify-self: center;
    height: 200px;
}

.currentgroup {
    grid-column: 1/-1;
    display: grid;
}

.forecastgroup1 {
    align-items: center;
    display: grid;
    justify-content: center;
    text-align: center;
    grid-column: 1/2;
    margin-top: 10px;
    margin-right: 10px;
}

.forecastgroup2 {
    align-items: center;
    display: grid;
    justify-content: center;
    text-align: center;   
    grid-column: 2/3;
    margin-top: 10px;
}

.forecastgroup3 {
    align-items: center;
    display: grid;
    justify-content: center;
    text-align: center;   
    grid-column: 3/4;
    margin-top: 10px; 
    margin-left: 10px;
}

.forecastgroups {
    grid-row: 3/4;
    grid-column: 1/-1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.forecasticon {
    justify-self: center;
}

.hourlyforecast {
    grid-column: 1/-1;
    grid-row: 4/5;
    display: grid;
    grid-template-columns: repeat(24, 1fr);
    overflow: scroll;
    height: 200px;
    margin-top: 10px;
    overflow-y: hidden;
}

#hour0 {
    grid-column: 1/2;
}

.hour {
    width: 200px;
    text-align: center;
    padding-top: 10px;
}

::-webkit-scrollbar {
    width: 12px;
  }
  
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 2000px white;
  }
  
  ::-webkit-scrollbar-thumb {
    background: #87CEEB;
    border: 5px double white;
  }`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,2DAA2D;AAC/D;;AAEA;IACI,aAAa;IACb,aAAa;IACb,iBAAiB;IACjB,qCAAqC;IACrC,kCAAkC;IAClC,gBAAgB;IAChB,yBAAyB;AAC7B;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,YAAY;IACZ,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;IACnB,2BAA2B;IAC3B,8BAA8B;IAC9B,4BAA4B;IAC5B,kBAAkB;IAClB,4BAA4B;IAC5B,+BAA+B;IAC/B,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;IACnB,2BAA2B;IAC3B,8BAA8B;IAC9B,kBAAkB;IAClB,iBAAiB;IACjB,2BAA2B;IAC3B,8BAA8B;IAC9B,4BAA4B;IAC5B,+BAA+B;IAC/B,cAAc;IACd,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;IACnB,2BAA2B;IAC3B,8BAA8B;IAC9B,iBAAiB;IACjB,kBAAkB;IAClB,2BAA2B;IAC3B,8BAA8B;IAC9B,4BAA4B;IAC5B,+BAA+B;IAC/B,cAAc;IACd,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;IACnB,2BAA2B;IAC3B,8BAA8B;IAC9B,iBAAiB;IACjB,2BAA2B;IAC3B,8BAA8B;IAC9B,cAAc;IACd,mBAAmB;AACvB;;AAEA;;;IAGI,yBAAyB;IACzB,YAAY;AAChB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,aAAa;IACb,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,gBAAgB;IAChB,iBAAiB;AACrB;;;AAGA;IACI,iBAAiB;IACjB,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,iBAAiB;AACrB;;;AAGA;IACI,gBAAgB;IAChB,aAAa;IACb,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,iBAAiB;IACjB,kBAAkB;AACtB;;;AAGA;IACI,aAAa;IACb,gBAAgB;IAChB,kBAAkB;IAClB,oBAAoB;IACpB,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,aAAa;AACjB;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IACvB,kBAAkB;IAClB,gBAAgB;IAChB,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IACvB,kBAAkB;IAClB,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IACvB,kBAAkB;IAClB,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,aAAa;IACb,qCAAqC;AACzC;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,iBAAiB;IACjB,aAAa;IACb,aAAa;IACb,sCAAsC;IACtC,gBAAgB;IAChB,aAAa;IACb,gBAAgB;IAChB